
          @import "src/styles/media-queries.scss";
        
@keyframes headerVisible {
  from {
    opacity: 0;
    transform: translate(0, -200%);
  }

  to {
    opacity: 1;
    transform: translate(0, 0);
  }
}

.headerVisible {
  animation: headerVisible 1s forwards;
}

.stageReward {
  flex: 1;
  display: flex;
  flex-direction: column;

  .stageReward__header {
    font-size: 26px;
    font-weight: 400;
    line-height: 40.74px;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
  }
  .stageReward__item {
    position: relative;
    margin: 0 auto;
    width: fit-content;
    // background-size: cover;
    // background-repeat: no-repeat;
    // background-position: center;

    .stageReward__item__frame {
      width: 100%;
      // max-width: 95%;
      max-width: 380px;
      // opacity: 0;
    }

    .rewardInfo {
      position: absolute;
      width: calc(100% - 48px);

      height: 100%;
      top: 0;
      left: 0;
      transform: translateX(24px);
      overflow: hidden;

      display: flex;
      align-items: center;
      justify-content: center;
      // gap: 8px;
      overflow-wrap: break-word;
      // word-break: break-all;
      word-wrap: break-word;

      .rewardInfo__flower {
        width: 45px;
        margin-right: 8px;

        img {
          width: 100%;
          height: auto;
          object-fit: contain;
          object-position: center;
        }
      }

      .rewardInfo__name {
        font-size: 36px;
        font-weight: 400;
        line-height: 32.4px;
        text-align: center;
        color: #ffdd00;
        max-width: calc(100% - 80px);
        overflow-wrap: break-word;
        // word-break: break-all;
        word-wrap: break-word;
        width: fit-content;

        @include xs {
          font-size: 24px;
          line-height: 120%;
        }
      }
    }
  }

  .inputPersonalInfo {
    margin: 8px auto;
    max-width: 90vw;

    font-family: "Montserrat Variable";
    font-size: 15px;
    font-style: italic;
    font-weight: 500;
    line-height: 18.29px;
    text-align: center;

    color: #fff;

    @include xs {
      margin: 4px auto 0;
    }
  }
}

.dataForm {
  margin-top: 20px;

  @include xs {
    margin-top: 10px;
  }

  .dataForm__name {
    width: fit-content;
    margin: 10px auto;
  }

  .dataForm__name__error {
    border: 1px solid #f00 !important;
  }

  .dataForm__name__noted {
    font-family: "Montserrat Variable";
    font-size: 13px;
    font-style: italic;
    font-weight: 500;
    line-height: 15.85px;
    text-align: center;
    color: #f00;
    width: 200px;
    margin: 10px auto;
  }

  .dataForm__phone {
    width: fit-content;
    margin: 10px auto;
  }

  .dataForm__phone__error {
    border: 1px solid #f00 !important;
  }
}

.actions {
  text-align: center;
  display: flex;
  justify-content: center;
  margin-top: 40px;

  @include xs {
    margin-top: 20px;
  }
}

.lastNoted {
  width: 240px;
  margin: 10px auto 60px;
  font-family: "Montserrat Variable";
  font-size: 13px;
  font-weight: 600;
  line-height: 15.85px;
  text-align: center;
  color: #fff;

  flex: 1;

  display: flex;
  align-items: flex-end;
}

@keyframes stageRewardVisible {
  from {
    opacity: 0;
    transform: scale(0.5);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.stageRewardVisible {
  opacity: 0;
  animation: stageRewardVisible 1s forwards;
  animation-delay: 0.5s;
}

.loading {
  position: fixed;
  z-index: 1000;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
}
