.button {
  position: relative;
  padding: 6px 68px;

  border-radius: 36px;
  display: flex;
  align-items: center;
  justify-content: center;

  // @include xs {
  //   padding: 4px 32px;
  // }

  cursor: pointer;

  &.buttonRed {
    &::before {
      background: #ff3737;
    }
  }

  &.buttonGray {
    &::before {
      background: #c9c9c9;
    }
  }

  &.buttonYellow {
    &::before {
      background: #ff9500;
    }
  }

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 0.7;
  }

  span {
    font-size: 40px;
    font-weight: 400;
    line-height: 62.68px;
    color: #fff;
    z-index: 2;
    position: relative;
    text-transform: uppercase;

    @include xs {
      font-size: 24px;
      font-weight: 400;
      line-height: 120%;
    }
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid #ffffff;
    z-index: 1;
    border-radius: 36px;
  }

  &:after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #ff8c8c 0.35%, #490001 100.59%);
    border: 1px solid #ffffff;
    z-index: 0;
    border-radius: 36px;
  }
}
