
          @import "src/styles/media-queries.scss";
        
.input {
  background: #000000d1;
  border: 1px solid #ffffff;
  border-radius: 18px;
  padding: 10px 16px;
  min-width: 250px;

  font-family: "Montserrat Variable";
  font-size: 16px;
  font-style: italic;
  font-weight: 700;
  line-height: 19.5px;
  color: #fff;
  text-transform: uppercase;

  &:disabled {
    color: #fff !important;
    opacity: 1 !important;
  }

  &::placeholder {
    font-family: "Montserrat Variable";
    font-size: 15px;
    font-style: italic;
    font-weight: 700;
    line-height: 18.29px;
    color: #b1b1bd;
    text-transform: uppercase;
  }
}
