
          @import "src/styles/media-queries.scss";
        
.luckyWheel {
  width: 100%;
  height: 100%;

  background-size: cover;
  background-position: center center;

  display: flex;
  flex-direction: column;

  overflow: hidden;

  .heading {
    flex: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
.luckyWheelVisible {
  animation: fadeIn 0.5s;
}
