.stageSpin {
  width: fit-content;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  flex: 1;
  justify-content: center;

  .wheelContainer {
    width: fit-content;
    position: relative;
  }

  .wheelHighLight {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    aspect-ratio: 1/1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: rotate(0deg);

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
      object-position: center;
      // transform: rotate(62.5deg);
      transform: rotate(107.5deg);
    }
  }

  .wheel {
    overflow: hidden;
    flex: 0;
    position: relative;
  }

  .spinOutbound {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    aspect-ratio: 1/1;
    object-fit: contain;
    object-position: center;
  }

  .actions {
    margin-top: 60px;
    // flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include gt-xs {
      margin-top: 100px;
    }
  }
}

@keyframes headerHidden {
  from {
    opacity: 1;
    transform: translate(0, 0);
  }

  to {
    opacity: 0;
    transform: translate(0, -200%);
  }
}
.header__hidden {
  animation: headerHidden 1s forwards;
}

@keyframes actionHidden {
  from {
    opacity: 1;
    transform: translate(0, 0);
  }

  to {
    opacity: 0;
    transform: translate(0, 200%);
  }
}
.actions__hidden {
  animation: actionHidden 1s forwards;
}

@keyframes stageSpinHidden {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0.5);
  }
}
.stageSpin__hidden {
  animation: stageSpinHidden 1s forwards;
  animation-delay: 2s;
}

@keyframes tadaHidden {
  0% {
    transform: scale(1);
  }
  10%,
  20% {
    transform: scale(0.9) rotate(-3deg);
  }
  30%,
  50%,
  70%,
  90% {
    transform: scale(1.1) rotate(3deg);
  }
  40%,
  60%,
  80% {
    transform: scale(1.1) rotate(-3deg);
  }
  100% {
    transform: scale(1) rotate(0);
  }
}
.wheelContainer__tada {
  animation: tadaHidden 1s backwards;
}
